
.company_footer {

	padding-top: 25px;
}
.rowlogo {
display: inline-flex;

}
.footerdivider {
	margin-top: 22px;
}
 .footercolor{

	background-image: linear-gradient(to top, #4a2c79 0%, #9d73c5 100%);
}

.footercolorsecond {
background-color: #4a2c79;

}

.footercolorsecond a {
	color: white;
}

.footercolorsecond:before {

	content: '';
    display: inline-block;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 62px 160px 0;
    border-color: transparent #5B5270 transparent transparent;
    position: absolute;
    top: 0;
    left: -62px;


}



.footer_company_span {
	color:white !important;


}
.company_footer p {
	color:white !important;
}
